import { JSX, Show, createEffect, createSignal, splitProps } from "solid-js";
import { InputProps } from "./types";
import { inputCss } from "./style";
import { CircleCloseFilled } from "solid-dada-icons";
import classNames from "classnames";
import { genPrefixClasses, useCacheCss } from "../theme";

export const Input = (props: InputProps) => {
  const [local, other] = splitProps(props, ["prefix", "class", "suffix"]);

  const [ref, setRef] = createSignal<HTMLInputElement>(null);

  const [focus, setFocus] = createSignal(false);
  const [value, setValue] = createSignal(props.value || "");

  createEffect(() => {
    setValue(props.value || "");
  });
  const cacheCss = useCacheCss(inputCss);

  const classes = () => {
    return classNames(
      cacheCss(),
      genPrefixClasses({
        input: true,
        [`input-${props.size}`]: !!props.size,
      }),
      {
        ["is-focus"]: focus(),
        ["is-disabled"]: props.disabled,
      },
      props.classList,
      props.class
    );
  };

  const handleFocus: JSX.FocusEventHandlerUnion<
    HTMLInputElement,
    FocusEvent
  > = (e) => {
    setFocus(true);
    if (typeof props.onFocus === "function") {
      return props.onFocus(e);
    }
    if (Array.isArray(props.onFocus)) {
      props.onFocus[1](props.onFocus[0], e);
    }
  };

  const onBlur: JSX.FocusEventHandlerUnion<HTMLInputElement, FocusEvent> = (
    e
  ) => {
    setFocus(false);
    if (typeof props.onBlur === "function") {
      return props.onBlur(e);
    }
    if (Array.isArray(props.onBlur)) {
      props.onBlur[1](props.onBlur[0], e);
    }
  };

  return (
    <div
      class={classes()}
      onClick={() => {
        ref().focus();
      }}
    >
      <Show when={props.prefix}>
        <div style={{ display: "flex", "margin-right": "8px" }}>
          {props.prefix}
        </div>
      </Show>
      <input
        {...other}
        ref={setRef}
        value={value()}
        onInput={(e) => {
          props.onChangeValue?.(e.target.value);
          setValue(e.target.value);
          if (ref() && "value" in props) {
            ref().value = props.value || "";
          }
        }}
        onFocus={handleFocus}
        onBlur={onBlur}
      />
      <Show when={props.clearable || props.suffix}>
        <Show when={props.clearable && value()}>
          <div
            class={classNames(
              genPrefixClasses({
                "input-clear-icon": true,
              })
            )}
            style={{
              height: "100%",
              display: "flex",
              cursor: "pointer",
              "align-items": "center",
            }}
            onClick={() => {
              props.onClear?.();
              if ("value" in props) {
                return;
              }
              setValue("");
            }}
          >
            <CircleCloseFilled />
          </div>
        </Show>
        <div style={{ display: "flex", "margin-right": "8px" }}>
          {props.suffix}
        </div>
      </Show>
    </div>
  );
};
