import { CloseBold } from "solid-dada-icons";
import { css } from "solid-styled-components";
import { ModalProps } from ".";
import { Show } from "solid-js";
import { genPrefixClasses } from "../theme";

type ModalHeaderProps = Pick<
  ModalProps,
  "onClose" | "title" | "closeIcon"
>;

export const Header = (props: ModalHeaderProps) => {
  return (
    <div
      classList={genPrefixClasses({
        "modal-header": true,
      })}
    >
      <Show when={props.closeIcon !== null && props.closeIcon !== false}>
        <Show when={!props.closeIcon} fallback={props.closeIcon}>
          <button
            classList={genPrefixClasses({
              "modal-close-icon": true,
            })}
            onClick={props.onClose}
          >
            <CloseBold />
          </button>
        </Show>
      </Show>
      <div
        class={css({
          marginBottom: "8px",
          fontSize: "16px",
          fontWeight: 600,
        })}
      >
        {props.title}
      </div>
    </div>
  );
};
