import { JSX, Show, createMemo } from "solid-js";
import { cardCss } from "./style";
import { genPrefixClasses, injectCss, useCacheCss } from "../theme";
import classNames from "classnames";

export type CardProps = {
  title?: JSX.Element;
  extra?: JSX.Element;
  style?: JSX.CSSProperties;
  children?: JSX.Element;
  bordered?: boolean;
  class?: string;
  hoverable?: boolean;
  classList?: {
    [k: string]: boolean | undefined;
  };
};

const Card = (props: CardProps) => {
  const cacheCss = useCacheCss(cardCss)
  const classes = () => {
    const cla = classNames(
      cacheCss(),
      genPrefixClasses({
        ["card-hoverable"]: props.hoverable,
        ["card-bordered"]: props.bordered,
        card: true,
      }),
      props.classList,
      props.class
    );
    return cla;
  };
  return (
    <div style={props.style} class={classes()}>
      <Show when={props.title || props.extra}>
        <div
          classList={{
            ...genPrefixClasses({
              ["card-header"]: true,
            }),
          }}
        >
          <div
            classList={{
              ...genPrefixClasses({
                ["card-header-wrapper"]: true,
              }),
            }}
          >
            <div
              classList={{
                ...genPrefixClasses({
                  ["card-header-title"]: true,
                }),
              }}
            >
              {props.title}
            </div>
            <div
              classList={{
                ...genPrefixClasses({
                  ["card-header-extra"]: true,
                }),
              }}
            >
              {props.extra}
            </div>
          </div>
        </div>
      </Show>
      <div
        classList={{
          ...genPrefixClasses({
            ["card-body"]: true,
          }),
        }}
      >
        {props.children}
      </div>
    </div>
  );
};

export { Card };
