import { buttonCss } from "./style";
import { ButtonProps } from "./types";
import { genPrefixClasses, useCacheCss } from "../theme";
import classNames from "classnames";
import { Show, splitProps } from "solid-js";

export const Button = (props: ButtonProps) => {
  const [local, reset] = splitProps(props, [
    "round",
    "circle",
    "classList",
    "class",
    "danger",
    "type",
    "size",
    "block",
    "loading",
  ]);
  const cacheCss = useCacheCss(buttonCss);
  const classes = () => {
    const cla = classNames(
      cacheCss(),
      genPrefixClasses({
        ["btn-loading"]: !!local.loading,
        ["btn-block"]: !!local.block,
        ["btn-default"]: !local.type,
        [`btn-${local.type}`]: !!local.type,
        [`btn-drangrous`]: !!local.danger,
        [`btn-round`]: local.round,
        [`btn-circle`]: local.circle,
        [`is-disabled`]: props.disabled,
        btn: true,
      }),
      props.classList,
      props.class
    );
    return cla;
  };
  return (
    <button
      {...reset}
      type={reset.htmlType}
      data-size={props.size}
      class={classes()}
    >
      <Show when={props.loading}>
        <span
          classList={genPrefixClasses({
            "btn-loading-icon": true,
          })}
        >
          <span
            role="img"
            aria-label="loading"
            classList={genPrefixClasses({
              'btn-spin': true
            })}
          >
            <svg
              viewBox="0 0 1024 1024"
              data-icon="loading"
              width="1em"
              height="1em"
              fill="currentColor"
              aria-hidden="true"
            >
              <path d="M988 548c-19.9 0-36-16.1-36-36 0-59.4-11.6-117-34.6-171.3a440.45 440.45 0 00-94.3-139.9 437.71 437.71 0 00-139.9-94.3C629 83.6 571.4 72 512 72c-19.9 0-36-16.1-36-36s16.1-36 36-36c69.1 0 136.2 13.5 199.3 40.3C772.3 66 827 103 874 150c47 47 83.9 101.8 109.7 162.7 26.7 63.1 40.2 130.2 40.2 199.3.1 19.9-16 36-35.9 36z"></path>
            </svg>
          </span>
        </span>
      </Show>
      {props.children}
    </button>
  );
};
export * from "./types";
