import { Properties as CSSProperties } from "csstype";
import { parse } from "./parse";
import { toHash } from "./to-hash";
import { update } from "./update";
import { getSheet } from "./get-sheet";
import { astish } from "./astish";

export interface CSSAttribute extends CSSProperties {
  [key: string]: CSSAttribute | string | number | undefined;
}

/**
 * In-memory cache.
 */
let cache: Record<string, string> = {};

/**
 * Stringifies a object structure
 * @param {Object} data
 * @returns {String}
 */
let stringify = (data) => {
  if (typeof data == "object") {
    let out = "";
    for (let p in data) out += p + stringify(data[p]);
    return out;
  } else {
    return data;
  }
};

export function hash(
  compiled: CSSAttribute,
  global: boolean,
  keyframes: boolean
): [string, string] {
  // Get a string representation of the object or the value that is called 'compiled'
  let stringifiedCompiled = stringify(compiled);
  // Retrieve the className from cache or hash it in place
  let className =
    cache[stringifiedCompiled] ||
    (cache[stringifiedCompiled] = toHash(stringifiedCompiled));
  if (!cache[className]) {
    // Build the _ast_-ish structure if needed
    let ast = stringifiedCompiled !== compiled ? compiled : astish(compiled);
    cache[className] = parse(
      keyframes ? { ["@keyframes " + className]: ast } : ast,
      global ? "" : "." + className
    );
  }
  return [className, cache[className]];
}

export function injectCss(css: string, append = false, cssToReplace?: string) {
    let ctx = this || {};
  update(css, getSheet(ctx.target), append, cssToReplace);
}
