import * as collapsible from "@zag-js/collapsible";
import { normalizeProps, useMachine } from "@zag-js/solid";
import {
  JSX,
  createEffect,
  createMemo,
  createUniqueId,
  on,
} from "solid-js";
import { collapseItemCss } from "./style";
import { genPrefixClasses, useCacheCss } from "../theme";
import classNames from 'classnames';

export type CollapseItemProps = {
  open?: boolean;
  defaultOpen?: boolean;
  disabled?: boolean;
  onOpenChange?: (details: collapsible.OpenChangeDetails) => void;
  onExitComplete?: () => void;
  classList?: string;
  class?: string;
  children?: JSX.Element;
};

export const CollapseItem = (props: CollapseItemProps) => {
  const id = createUniqueId();
  const machine = collapsible.machine({
    open: props.defaultOpen,
    id,
    onOpenChange: props.onOpenChange,
    onExitComplete: props.onExitComplete,
  });
  const [state, send] = useMachine(machine);
  const api = createMemo(() => {
    return collapsible.connect(
      { ...state, context: { ...state.context, disabled: props.disabled } },
      send,
      normalizeProps
    );
  });
  createEffect(
    on([() => props.open], () => {
      if ("open" in props) {
        props.open ? api().open() : api().close();
      }
    })
  );
  const cacheCss = useCacheCss(collapseItemCss)
  const classes = () => {
    const cla = classNames(
      cacheCss(),
      genPrefixClasses({
        card: true,
      }),
      props.classList,
      props.class
    );
    return cla;
  };
  return (
    <div
      {...api().rootProps}
      class={classes()}
    >
      <div {...api().contentProps}>{props.children}</div>
    </div>
  );
};
