import { JSX, Show, mergeProps } from "solid-js";
import { Contain, ContainProps } from "./contain";
import { Popup, PopupProps } from "./popup";
import { useStyle } from "./useStyle";
import { genPrefixClasses, useCacheCss } from "../theme";
import { drawerCss } from "./style";
import classNames from "classnames";
import { Close } from "solid-dada-icons";

export * from "./contain";

export type Placement = "left" | "top" | "right" | "bottom";

export type DrawerProps = ContainProps &
  Omit<PopupProps, "panelTransition" | "panelStyle" | "class" | "classList"> & {
    width?: string;
    height?: string;
    placement?: Placement;
    title?: JSX.Element;
    closeIcon?: JSX.Element | null;
    extra?: JSX.Element;
  };

export const Drawer = (props: DrawerProps) => {
  const merged = mergeProps(
    {
      appendToBody: true,
      placement: "right" as Placement,
      closable: true,
      maskClosable: true,
      width: "378px",
      height: "378px",
    },
    props
  );
  const panel = useStyle(merged);
  let ref: HTMLDivElement | undefined = undefined;

  const cacheCss = useCacheCss(() => drawerCss(merged.width, merged.height));
  const classes = () => {
    return classNames(
      cacheCss(),
      genPrefixClasses({
        drawer: true,
        [`drawer-${merged.placement}`]: true,
        "drawer-vertical": panel.vertical(),
      })
    );
  };

  return (
    <Contain
      {...merged}
      popup={
        <Popup
          onClick={(e) => {
            if (
              ref &&
              !(e.target === ref || ref.contains(e.target)) &&
              merged.maskClosable
            ) {
              props.onClose?.(e);
            }
          }}
          {...merged}
          class={classes()}
          panelTransition={panel.panelTransition}
        >
          <div
            ref={ref}
            classList={genPrefixClasses({
              "drawer-content": true,
            })}
          >
            <Show when={props.title || props.closeIcon}>
              <div
                classList={genPrefixClasses({
                  "drawer-header": true,
                })}
              >
                <span
                  classList={genPrefixClasses({
                    "drawer-header-close-icon": true,
                  })}
                  onClick={(e) => props.onClose?.(e)}
                >
                  <Close />
                </span>
                <div
                  classList={genPrefixClasses({
                    "drawer-header-title": true,
                  })}
                >
                  {props.title}
                </div>
                <Show when={props.extra}>{props.extra}</Show>
              </div>
            </Show>
            <div
              classList={genPrefixClasses({
                "drawer-body": true,
              })}
            >
              {props.children}
            </div>
          </div>
        </Popup>
      }
    />
  );
};
