import { CSSAttribute, css, genStyle } from "../../theme";
import { Properties } from "csstype";
import { AliasToken } from "../../theme/interface";
import { ComponentToken } from "../../theme/types";

export const resetComponent = (
  token: AliasToken,
  needInheritFontFamily = false
): Properties => ({
  boxSizing: "border-box",
  margin: 0,
  padding: 0,
  color: token.colorText,
  fontSize: token.fontSize + "px",
  // font-variant: @font-variant-base;
  lineHeight: token.lineHeight,
  listStyle: "none",
  // font-feature-settings: @font-feature-settings-base;
  fontFamily: needInheritFontFamily ? "inherit" : token.fontFamily,
});

export const cardHeaderCss = (
  token: ComponentToken<{
    cardPaddingBase: number;
    headerHeight: number;
  }>
): CSSAttribute => {
  return {
    [`.${token.componentCls}-header`]: {
      display: "flex",
      justifyContent: "center",
      minHeight: token.headerHeight + 'px',
      marginBottom: '-1px',
      paddingInline: token.cardPaddingBase + 'px',
      borderBottom: `${token.lineWidth}px ${token.lineType} ${token.colorBorderSecondary}`,
      borderRadius: `${(token.borderRadiusLG)}px ${(token.borderRadiusLG)}px 0 0`,
    },
    [`.${token.componentCls}-header-wrapper`]: {
      width: "100%",
      display: "flex",
      alignItems: "center",
    },
    [`.${token.componentCls}-header-title`]: {
      flex: 1,
      overflow: "hidden",
      whiteSpace: "nowrap",
      textOverflow: "ellipsis",
    },
    [`.${token.componentCls}-header-extra`]: {},
  };
};

export const cardCss = () => {
  return genStyle("card", (token) => {
    const cardToken = {
      ...token,
      cardShadow: token.boxShadowCard,
      cardHeadPadding: token.padding,
      cardPaddingBase: token.paddingLG,
      cardActionsIconSize: token.fontSize,
      cardPaddingSM: 12, // Fixed padding.
      headerBg: 'transparent',
      headerFontSize: token.fontSizeLG,
      headerFontSizeSM: token.fontSize,
      headerHeight: token.fontSizeLG * token.lineHeightLG + token.padding * 2,
      headerHeightSM: token.fontSize * token.lineHeight + token.paddingXS * 2,
      actionsBg: token.colorBgContainer,
      actionsLiMargin: `${token.paddingSM}px 0`,
      tabsMarginBottom: -token.padding - token.lineWidth,
      extraColor: token.colorText,
    };
    return css({
      ...resetComponent(token),
      ...cardHeaderCss(cardToken),
      position: "relative",
      background: token.colorBgContainer,
      borderRadius: token.borderRadiusLG + "px",
      [`.${token.componentCls}-body`]: {
        padding: cardToken.cardPaddingBase + "px",
      },
      [`&.${token.componentCls}-bordered`]: {
        border: `${token.lineWidth}px ${token.lineType} ${token.colorBorderSecondary}`,
      },
      [`&:not(.${token.componentCls}-bordered)`]: {
        boxShadow: token.boxShadowTertiary,
      },
      [`&.${token.componentCls}-hoverable:hover`]: {
        position: 'relative',
        zIndex: 1,
        boxShadow: cardToken.cardShadow,
      },
    });
  });
};
