import { css, keyframes, genStyle } from "../../theme";
import { ComponentToken } from "../../theme/types";
import { CollapseToken } from "./token";

const prepareToken = (token: ComponentToken<CollapseToken>) => ({
  headerPadding: `${token.paddingSM}px ${token.padding}px`,
  headerBg: token.colorFillAlter,
  contentPadding: `${token.padding}px 16px`, // Fixed Value
  contentBg: token.colorBgContainer,
});

export const collapseCss = () => {
  return genStyle<CollapseToken>(
    "collapse",
    (token) => {
      return css({
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        cursor: "pointer",
        background: token.headerBg,
        padding: token.headerPadding,
        borderRadius: "4px",
      });
    },
    prepareToken
  );
};
export const collapseItemCss = () => {
  return genStyle(
    "collapse-item",
    (token) => {
      const expand = keyframes(`
      from {
        height: 0;
      }
      to {
        height: var(--height);
      }
      `);
      const collapse = keyframes(`
      from {
        height: var(--height);
      }
      to {
        height: 0;
      }
      `);
      const [className, cssStyle] = css({
        '[data-scope="collapsible"][data-part="content"]': {
          overflow: "hidden",
        },
        '[data-scope="collapsible"][data-part="content"][data-state="open"]': {
          animation: `${expand[0]} 110ms cubic-bezier(0, 0, 0.38, 0.9)`,
        },
        '[data-scope="collapsible"][data-part="content"][data-state="closed"]':
          {
            animation: `${collapse[0]} 110ms cubic-bezier(0, 0, 0.38, 0.9)`,
          },
      });
      return [className, `${cssStyle}${expand[1]}${collapse[1]}`];
    },
  );
};
