import { CSSAttribute, css, genStyle } from "../../theme";
import { ComponentToken } from "../../theme/types";

export const containerCss = () => {
  return genStyle("popup-container", (token) => {
    return css({
      inset: 0,
      zIndex: 999,
      outline: "none",
      pointerEvents: "none",
      position: "absolute",
      [`&.${token.componentCls}-is-opened`]: {
        pointerEvents: "auto",
      },
      [`&.${token.componentCls}-append-body`]: {
        position: "fixed",
      },
    });
  });
};

const drawerHeaderCss = (token: ComponentToken<{}>): CSSAttribute => {
  const { calc } = token;
  return {
    [`.${token.componentCls}-header`]: {
      display: "flex",
      flex: 0,
      alignItems: "center",
      padding: `${token.padding}px ${token.paddingLG}px`,
      fontSize: token.fontSizeLG + "px",
      lineHeight: token.lineHeightLG,
      borderBottom: `${token.lineWidth}px ${token.lineType} ${token.colorSplit}`,
    },
    [`.${token.componentCls}-header-title`]: {
      display: "flex",
      flex: 1,
      alignItems: "center",
      minWidth: 0,
      minHeight: 0,
    },
    [`.${token.componentCls}-header-close-icon`]: {
      display: "inline-flex",
      width: calc(token.fontSizeLG).add(token.paddingXS).equal() + "px",
      height: calc(token.fontSizeLG).add(token.paddingXS).equal() + "px",
      borderRadius: token.borderRadiusSM + "px",
      justifyContent: "center",
      alignItems: "center",
      marginInlineEnd: token.marginXS + "px",
      color: token.colorIcon,
      fontWeight: token.fontWeightStrong,
      fontSize: token.fontSizeLG + "px",
      fontStyle: "normal",
      lineHeight: 1,
      textAlign: "center",
      textTransform: "none",
      textDecoration: "none",
      background: "transparent",
      border: 0,
      cursor: "pointer",
      transition: `all ${token.motionDurationMid}`,
      textRendering: "auto",
      "&:hover": {
        color: token.colorIconHover,
        backgroundColor: token.colorBgTextHover,
        textDecoration: "none",
      },
      "&:active": {
        backgroundColor: token.colorBgTextActive,
      },
    },
  };
};

const bodyCss = (token: ComponentToken<{}>): CSSAttribute => {
  return {
    [`.${token.componentCls}-body`]: {
      flex: 1,
      minWidth: 0,
      minHeight: 0,
      padding: token.paddingLG + "px",
      overflow: "auto",
    },
  };
};

export const drawerCss = (width: string, height: string) => {
  return genStyle("drawer", (token) => {
    return css({
      "pointer-events": "auto",
      [`.${token.componentCls}-content`]: {
        "box-shadow":
          "-6px 0 16px 0 rgba(0, 0, 0, 0.08), -3px 0 6px -4px rgba(0, 0, 0, 0.12), -9px 0 28px 8px rgba(0, 0, 0, 0.05)",
        position: "absolute",
        width,
        height: "100%",
        top: 0,
        zIndex: token.zIndexPopupBase,
        background: "#fff",
      },
      ...drawerHeaderCss(token),
      ...bodyCss(token),
      [`&.${token.componentCls}-right`]: {
        [`.${token.componentCls}-content`]: {
          width,
          right: 0,
        },
      },
      [`&.${token.componentCls}-left`]: {
        [`.${token.componentCls}-content`]: {
          width,
          left: 0,
        },
      },
      [`&.${token.componentCls}-bottom`]: {
        [`.${token.componentCls}-content`]: {
          width,
          top: "unset",
          bottom: 0,
        },
      },
      [`&.${token.componentCls}-vertical`]: {
        [`.${token.componentCls}-content`]: {
          width: "100%",
          height,
          top: undefined,
          boxShadow:
            "0px 16px 48px 16px rgba(0, 0, 0, .08), 0px 12px 32px rgba(0, 0, 0, .12), 0px 8px 16px -8px rgba(0, 0, 0, .16)",
        },
      },
    });
  });
};
