import { toArray } from "happy-utils";
import { CollapseProvider, useCollapseContext } from "./context";
import { JSX, ParentProps, createSignal } from "solid-js";
import { CollapseItem, CollapseItemProps } from "./collapse-item";
import { ArrowRight } from "solid-dada-icons";
import { css } from "solid-styled-components";
import { genPrefixClasses, useCacheCss } from "../theme";
import classNames from 'classnames'
import { collapseCss } from "./style";

export type CollapseProps = {
  activeKey?: string | string[];
  defaultActiveKey?: string | string[];
  accordion?: boolean;
  onChange?: (keys: string[]) => void;
};

const Panel = (
  props: CollapseItemProps & {
    class?: string;
    classList?: {
      [k: string]: boolean | undefined
    }
    style?: JSX.CSSProperties;
    label?: JSX.Element
    key: string;
  }
) => {
  const context = useCollapseContext();
  if (!("key" in props)) {
    console.warn("panel key is empty");
  }
  const [open, setOpen] = createSignal(false);

  const isOpened = () => {
    if (context) {
      return context.activeKeys().includes(props.key);
    }
    return open();
  };

  const handleOpen = () => {
    if (props.disabled) {
      return;
    }
    if (context) {
      context.onChange(props.key);
      return;
    }
    setOpen(!open());
  };

  const cacheCss = useCacheCss(collapseCss)
  const classes = () => {
    const cla = classNames(
      cacheCss(),
      genPrefixClasses({
        card: true,
      }),
      props.classList,
      props.class
    );
    return cla;
  };

  return (
    <>
      <div
        onClick={handleOpen}
        class={classes()}
      >
        <div>{props.label}</div>
        <div>
          <ArrowRight
            class={css({
              transition: "all .15s",
              transform: `rotate(${isOpened() ? "90deg" : "0deg"})`,
            })}
          />
        </div>
      </div>
      <CollapseItem open={isOpened()}>
        <div
          class={css({
            padding: "12px",
          })}
        >
          {props.children}
        </div>
      </CollapseItem>
    </>
  );
};

const Collapse = (props: ParentProps<CollapseProps>) => {
  const [activeKeys, setActiveKeys] = createSignal(
    toArray<string>(props.defaultActiveKey)
  );
  const isOpened = (key: string) => activeKeys().includes(key);
  return (
    <CollapseProvider
      value={{
        activeKeys,
        onChange: (key) => {
          let keys: string[] = [];
          if (props.accordion) {
            keys = isOpened(key) ? [] : [key];
          } else {
            keys = isOpened(key)
              ? activeKeys().filter((k) => k !== key)
              : activeKeys().concat(key);
          }
          if (!props.activeKey) {
            setActiveKeys(keys);
          }
          props.onChange?.(keys);
        },
      }}
    >
      {props.children}
    </CollapseProvider>
  );
};
Collapse.Panel = Panel

export { Collapse }
